import ApiClient from "@engie/apiclient";

const client = new ApiClient({
  baseURL: "https://es2-rec1-out.gses.gdfsuez.net:8443/employees_calendar"
});

export default {
  getEmployees() {
    return client.get("/api/v1/employees");
  }
};



